







































































import { Component, Vue } from 'vue-property-decorator';
import Clipboard from 'clipboard';
import { guess, reset } from '../guess';
import constant from '../constant';

@Component
export default class Home extends Vue {
  form = {
    num: '',
  };

  open = false;

  openDialog = false;

  color = 'error';

  icon = 'warning';

  message = '';

  constant = constant;

  code = '';

  sample = reset();

  async submit() {
    const [r, i, strange] = await guess(this.form.num);
    this.message = i;
    if (strange === 'niconiconi') {
      this.form.num = i;
    } else if (r) {
      this.openDialog = true;
      this.code = strange;
    } else {
      this.color = 'error';
      this.icon = 'warning';
      this.open = true;
      setTimeout(() => {
        this.open = false;
      }, 3000);
    }
  }

  async copy() {
    const cp = new Clipboard('#copy');
    cp.on('success', () => {
      this.openDialog = false;
    });
    cp.on('error', (e) => {
      this.message = `${e}`;
      this.openDialog = false;
    });
  }
}
